<template>
    <div class="row">
    <!--right-->
        <div data-app class="col-12">                   
            <div class="form-group">
            <!--live search-->           
                <div style="display:flex;flex-direction:row;">
                    <div class="table-responsive col-6 px0">
                        <label style="font-size:90%">Select Department</label>
                        <select v-model="dept" class="form-control" v-if="dept_ready" @change="new_list">
                            <option v-for="dep in dept_list ">{{ dep.department }}</option>
                        </select>                                      
                    </div>
                    <div class="col-2"></div>
                    <div class="col-2">
                        <label>School Year</label>
                        <input type="number" class='form-control' v-model="st" @change="update_yr"/>
                    </div>
                    <div class="col-2">
                        <label>&nbsp;</label>
                        <input type="number" class='form-control' v-model="ed" readonly/>
                    </div>
                </div>
            </div>        
            <v-data-table
            :headers="headers"
            :items="assetlist"
            class="elevation-1"
            :rows-per-page-items="[10]"
            >
            <template v-slot:items="props">
                <td>
                    <a href="" :id= props.item.id v-on:click.prevent="open_overlay(props.item)">Edit</a><br/>
                    <a href="" :id= props.item.id v-on:click.prevent="remove(props.item.code)">Remove</a>
                </td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.code }}</td>
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>
            </template>
            </v-data-table><br/>
            <!--member-->
            <div class="form-group" v-if="open_editor==false">         
                <div style="display:flex;flex-direction:row;">
                    <label style="margin-top:0.25em;margin-right:0.5em">Insert New Asset :</label>
                    <input type='text' class='form-control' v-model="insert_asset_name" maxlength="80" placeholder="Asset Name" style="width:18em;margin-right:1em"/>    
                    <input type='text' class='form-control' v-model="insert_asset_code" maxlength="80" placeholder="Asset Code" style="width:10em"/>                   
                    <input type='button' class='form-control' value="Insert" @click="new_asset_insert" style='margin-left:auto;width:10em'/>
                </div>
            </div>
        </div>
        <div v-if="open_editor" class="col-12">
            <form @submit.prevent="edit_asset(editing_asset)">
                <div class="card">
                    <div class="card-header">
                        <strong>Edit Asset Category - {{ editing_asset }}</strong>
                    </div>
                    <div class="card-body">
                        <div class="flex col-12 px0" style='margin-bottom:8px;'>
                            <div class="col-4">
                                Asset Name
                            </div>
                            <div class="col-8">
                                <input type="text" class="form-control" v-model="editdata.name"/>
                            </div>
                        </div>
                        <div class="flex col-12 px0">
                            <div class="col-4">
                                Asset Code
                            </div>
                            <div class="col-8">
                                <input type="text" class="form-control" v-model="editdata.code"/>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer">
                        <button type="submit" class="btn btn-primary btn-sm">
                            <i class="fa fa-paper-plane"></i> Submit
                        </button>
                        <button type="button" class="btn btn-danger btn-sm" @click="close_overlay">
                            <i class="fa fa-times"></i> Close
                        </button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            editdata:{
                name:'',
                code:'',
            },
            editing_asset:'',
            open_editor:false,
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Asset Category',value:'name',sortable: false},
                { text: 'Asset Code',value:'max',sortable: false},
            ], 
            dept_list:[],
            dept:'',
            dept_ready:false,
            assetlist:[],
            st:'',
            ed:'',
            insert_fund_name:'',
            fund_current : 'Regular',
            fund_type:['Regular','Special'],
            user_check:'',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<3){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].department;
                this.dept_ready = true;
                console.log(this.dept_list)
            }
        );
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                this.st = response.data.st.substring(0,4);
                this.ed = response.data.ed.substring(0,4);
            }
        );    
    this.fetchdata();  
},
methods:{
    close_overlay(){
        this.open_editor = false;
    },
    open_overlay(item){
        this.editing_asset = item.code;
        this.editdata.name = item.name;
        this.editdata.code = item.code;
        this.open_editor = true;
    },
    async edit_asset(code){
        var formData = new FormData();
        formData.append('original_code',this.editing_asset);
        formData.append('code',this.editdata.code);
        formData.append('name',this.editdata.name);
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        await axios.post('Procurement/department/backend/update_asset.php',formData)
            .then(
                (response)=>{
                    if(response.data=='OK'){
                        this.$alert("Successfully updated record").then(
                            ()=>{
                                this.fetchdata();
                                this.open_editor = false;
                            }
                        )
                    }
                    
                }
            ) 
    },
    async fetchdata(){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        await axios.post('Procurement/department/backend/assetlist.php',formData)
            .then(
                (response)=>{
                    this.assetlist = response.data;
                    this.open_editor = false;
                }
            )  
    },
    update_yr(){
        this.ed = parseInt(this.st)+1;
        this.new_list();
    },
    new_list(event){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/department/backend/assetlist.php',formData)
            .then(
                (response)=>{
                    this.assetlist = response.data;
                    this.open_editor = false;
                }
            ) 
    },
    new_asset_insert(event){
        var formData = new FormData();
        formData.append('name',this.insert_asset_name);
        formData.append('dept',this.dept);
        formData.append('code',this.insert_asset_code);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/department/backend/new_asset.php',formData)
        .then(
            (response)=>{
                this.fetchdata();
            }
        );
        this.insert_asset_name = '';
        this.insert_asset_code = '';
    },
    remove(targetID){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('id',targetID);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/department/backend/remove_asset.php',formData)
        .then(
            (response)=>{
                if(response.data!="OK"){
                    this.$alert(response.data).then(
                        ()=>{
                            this.fetchdata();
                        }
                    )
                } else {
                    this.fetchdata();
                }              
            }
        )
    }
}
}
</script>



